import React, { useState, useEffect, useCallback } from "react";
import { Row, Col, Tabs, Typography } from "antd";
import SearchBox from "../SearchBox";
import CalendarBox from "../CalendarBox";
import ProductsTable from "../ProductsTable";
import newdata from "../../data/products-visely.json";
import previouslyExcludedData from "../../data/previously-excluded.json";

const CollectionPage = () => {
  const [openDatePicker, setOpenDatePicker] = useState(false);
  const { TabPane } = Tabs;
  const { Title } = Typography;

  const handleScroll = useCallback(() => {
    setOpenDatePicker(false);
  }, []);

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
  }, [handleScroll]);

  const handleDatePicker = () => {
    setOpenDatePicker((prevState) => !prevState);
  };

  return (
    <Row>
      <Col span={18}>
        <SearchBox
          hintLabel="SEARCH"
          hintTitle="— Pin or exclude products for specific search terms or SKU. Overwrite the search algorithm."
          label="Search term"
          tooltipTitle="Example"
          placeholder="Dress"
          marginBottom={6}
        />
        <Tabs defaultActiveKey="0" tabPosition={"top"}>
          {newdata.facets[0].values.map((item, i) => (
            <TabPane
              tab={`${item.name} (${item.count})`}
              key={i}
              disabled={i === 28}
            >
              <ProductsTable
                newdata={newdata}
                openDatePicker={openDatePicker}
                handleDatePicker={handleDatePicker}
              />
              <Title level={5} style={{ margin: "21px 0 0" }}>
                Previously Excluded
              </Title>

              <ProductsTable
                newdata={previouslyExcludedData}
                openDatePicker={openDatePicker}
                handleDatePicker={handleDatePicker}
              />
            </TabPane>
          ))}
        </Tabs>
      </Col>
      <Col span={6} style={{ padding: "105px 0 0 26px" }}>
        <CalendarBox
          openDatePicker={openDatePicker}
          setOpenDatePicker={setOpenDatePicker}
        />
      </Col>
    </Row>
  );
};
export default CollectionPage;
