import React, { useState } from "react";
import { Table, Typography, Button, Form, Input } from "antd";
import classes from "./styles.module.scss";
import {
  PushpinOutlined,
  CalendarOutlined,
  CloseOutlined,
  ArrowUpOutlined,
  ArrowDownOutlined,
} from "@ant-design/icons";

const ProductsTable = (props) => {
  const { newdata, openDatePicker, handleDatePicker } = props;
  const [state, setState] = useState({
    selectedRowKeys: [],
  });

  const { selectedRowKeys } = state;
  const { Text } = Typography;

  const data = [];

  newdata?.products.forEach((product, index) => {
    data.push({
      index,
      key: product.id,
      name: product.name,
      status: product.inStock,
      media: product.media[0].url,
    });
  });

  const columns = [
    {
      title: () => (
        <div className={classes.theadCell}>
          <div className={classes.theadCellRow}>
            <div className={classes.selected}>
              {state.selectedRowKeys.length} selected
            </div>
            <Button
              type="default"
              icon={<PushpinOutlined className={classes.theadButtonIcon} />}
              className={classes.theadButton}
            />
            <Button
              type={openDatePicker ? "primary" : "default"}
              icon={<CalendarOutlined className={classes.theadButtonIcon} />}
              className={classes.theadButton}
              onClick={handleDatePicker}
            />
            <Button
              disabled
              icon={<CloseOutlined className={classes.theadButtonIcon} />}
              className={classes.theadButton}
            />
          </div>

          <Form layout={"horizontal"} className={classes.form}>
            <Form.Item
              label={"Narrow down"}
              tooltip={{
                title: "Example",
              }}
              style={{ margin: "0" }}
            >
              <Input placeholder={"Input placeholder"} />
            </Form.Item>
          </Form>
        </div>
      ),
      dataIndex: "name",
      key: "name",
      render: (text, row, index) => (
        <div className={classes.product}>
          <div className={classes.col}>
            <div className={classes.imgBox}>
              <img src={row.media} alt="" className={classes.img} />
            </div>
            <Text className={classes.name}>{row.name.en}</Text>
          </div>
          <div className={classes.col}>
            <div className={classes.dateBox}>
              <CalendarOutlined className={classes.calendarIcon} />
              Dec 6 - Dec 19
            </div>
            <div className={classes.buttonBox}>
              {data.length > 1 && (
                <>
                  <Button
                    disabled={row.index === 0 && true}
                    className={classes.posButton}
                    icon={<ArrowUpOutlined className={classes.posButtonIcon} />}
                  />
                  <Button
                    disabled={row.index === data.length - 1 && true}
                    className={classes.posButton}
                    icon={
                      <ArrowDownOutlined className={classes.posButtonIcon} />
                    }
                  />
                </>
              )}
            </div>
          </div>
        </div>
      ),
    },
  ];

  const onSelectChange = (selectedRowKeys) => {
    setState({ selectedRowKeys });
  };

  const rowSelection = {
    selectedRowKeys,
    onChange: onSelectChange,
  };

  return (
    <Table
      bordered={true}
      rowSelection={rowSelection}
      columns={columns}
      dataSource={data}
      pagination={{
        position: ["bottomLeft"],
        defaultPageSize: 10,
        showSizeChanger: true,
        pageSizeOptions: ["10", "20", "30"],
        hideOnSinglePage: true,
      }}
    />
  );
};
export default ProductsTable;
